body, input, h1, p {
    background-color: black;
    color:chartreuse;
    font-family: 'Roboto Mono', monospace;
    font-size:16px;
    font-weight: 100;
    padding: 0;
    margin: 0;
    border: 0;
    @media screen and(min-width:767px) {
        font-size:18px;
    }
}
.indexframe {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display:flex;
    flex-direction: column;
}

.main {
    flex-grow: 1;
    border-bottom: 1px solid chartreuse;
    border-top: 1px solid chartreuse;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap:20px;
    padding:15px;
}
.chat-main-frame {
    padding-left:5px;
    padding-right:5px;
    margin-top: 40px;
    @media screen and(min-width:767px) {
        margin-top: 60px;
        padding-left:15px;
        padding-right:15px;
    }
}
.intro {
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top:100px;
}
.buttons {
    display: flex;
    flex-direction: column;
    gap:30px;
    @media screen and(min-width:767px) {
        flex-direction: row;
    }
}
.btn {
    background-color: chartreuse;
    font-family: 'Roboto Mono', monospace;
    font-size:18px;
    font-weight: 100;
    color:black;
    padding:20px;
    border:0;
    text-decoration: none;
}

.header, .footer {
    position:fixed;
    left:0;
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    width:100%;
    & p {
        padding:5px;
    }
    @media screen and(min-width: 767px) {
        flex-direction: row;
        justify-content: space-between;
        & p {
            padding:15px;
        }
    }

}
.header {
    top:0;
    border-bottom: 1px solid chartreuse;
}
.footer {
    bottom:0;
    border-top: 1px solid chartreuse;
}

.input-data {
    flex-grow:1;
    outline: none;
}
#output {
    //background-color: aliceblue;
}
#input {
    //background-color: aqua;
    margin-bottom: 60px;
}

.one-string-form {
    display:flex;
    gap:5px;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}