body, input, h1, p {
  color: #7fff00;
  background-color: #000;
  border: 0;
  margin: 0;
  padding: 0;
  font-family: Roboto Mono, monospace;
  font-size: 16px;
  font-weight: 100;
}

@media screen and (min-width: 767px) {
  body, input, h1, p {
    font-size: 18px;
  }
}

.indexframe {
  width: 100%;
  height: 100%;
  flex-direction: column;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.main {
  border-top: 1px solid #7fff00;
  border-bottom: 1px solid #7fff00;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 15px;
  display: flex;
}

.chat-main-frame {
  margin-top: 40px;
  padding-left: 5px;
  padding-right: 5px;
}

@media screen and (min-width: 767px) {
  .chat-main-frame {
    margin-top: 60px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.intro {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  display: flex;
}

.buttons {
  flex-direction: column;
  gap: 30px;
  display: flex;
}

@media screen and (min-width: 767px) {
  .buttons {
    flex-direction: row;
  }
}

.btn {
  color: #000;
  background-color: #7fff00;
  border: 0;
  padding: 20px;
  font-family: Roboto Mono, monospace;
  font-size: 18px;
  font-weight: 100;
  text-decoration: none;
}

.header, .footer {
  width: 100%;
  background-color: #000;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: fixed;
  left: 0;
}

.header p, .footer p {
  padding: 5px;
}

@media screen and (min-width: 767px) {
  .header, .footer {
    flex-direction: row;
    justify-content: space-between;
  }

  .header p, .footer p {
    padding: 15px;
  }
}

.header {
  border-bottom: 1px solid #7fff00;
  top: 0;
}

.footer {
  border-top: 1px solid #7fff00;
  bottom: 0;
}

.input-data {
  outline: none;
  flex-grow: 1;
}

#input {
  margin-bottom: 60px;
}

.one-string-form {
  gap: 5px;
  display: flex;
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

